import apiConfig from '../config/apiConfig';

// URL base de la API
const API_URL = apiConfig.API_URL;

// Token de autenticacin
let token = localStorage.getItem('token') || '';

/**
 * Configura el token de autenticacin
 * @param {string} newToken 
 */
const setToken = (newToken) => {
  token = newToken;
  if (newToken) {
    localStorage.setItem('token', newToken);
  } else {
    localStorage.removeItem('token');
  }
};

/**
 * Realiza una peticin GET a la API
 * @param {string} endpoint - Ruta de la API
 * @param {Object} options - Opciones adicionales
 * @returns {Promise<any>} - Respuesta de la API
 */
const get = async (endpoint, options = {}) => {
  try {
    const headers = {
      'Content-Type': 'application/json',
      ...(token && { 'Authorization': `Bearer ${token}` }),
      ...(options.headers || {})
    };
    
    const response = await fetch(`${API_URL}${endpoint}`, {
      method: 'GET',
      headers,
      ...options
    });
    
    if (options.responseType === 'blob') {
      return response.blob();
    }
    
    // Si la respuesta no es JSON, devolver el texto
    const contentType = response.headers.get('content-type');
    if (!contentType || !contentType.includes('application/json')) {
      const text = await response.text();
      return {
        success: response.ok,
        data: text,
        status: response.status
      };
    }
    
    const data = await response.json();
    
    // Si la respuesta tiene un cdigo 401 o 403, podra ser un problema de autenticacin
    if (response.status === 401 || response.status === 403) {
      console.warn(`Error de autenticacin en ${endpoint}:`, data);
      
      // Si estamos en desarrollo, permitir continuar con una respuesta simulada
      if (process.env.NODE_ENV === 'development') {
        console.log('Modo desarrollo: devolviendo respuesta simulada para', endpoint);
        return {
          success: false,
          error: data.message || 'Error de autenticacin',
          status: response.status,
          simulatedResponse: true
        };
      }
    }
    
    if (!response.ok) {
      throw new Error(data.message || 'Error en la peticin');
    }
    
    return data;
  } catch (error) {
    console.error(`Error en GET ${endpoint}:`, error);
    throw error;
  }
};

/**
 * Realiza una peticin POST a la API
 * @param {string} endpoint - Ruta de la API
 * @param {Object} body - Cuerpo de la peticin
 * @param {Object} options - Opciones adicionales
 * @returns {Promise<any>} - Respuesta de la API
 */
const post = async (endpoint, body = {}, options = {}) => {
  try {
    const headers = {
      'Content-Type': 'application/json',
      ...(token && { 'Authorization': `Bearer ${token}` }),
      ...(options.headers || {})
    };
    
    const response = await fetch(`${API_URL}${endpoint}`, {
      method: 'POST',
      headers,
      body: JSON.stringify(body),
      ...options
    });
    
    const data = await response.json();
    
    if (!response.ok) {
      throw new Error(data.message || 'Error en la peticin');
    }
    
    return data;
  } catch (error) {
    console.error(`Error en POST ${endpoint}:`, error);
    throw error;
  }
};

