// apiConfig.js - Configuracin centralizada para URLs de API

// Determinar el entorno actual 
const isProd = process.env.NODE_ENV === 'production';

// Verificar si hay una API URL especificada en localStorage
const storedApiUrl = localStorage.getItem('apiUrl');

// API real sin subdirectorios 'api' o 'raw' al final
const apiDomain = storedApiUrl || (isProd ? 'https://websap-backend.onrender.com' : 'http://localhost:3000');

// Configuracin para entorno de produccin y desarrollo
const config = {
  // URLs para produccin (servidor real)
  production: {
    API_URL: `${apiDomain}/api`,
    RAW_URL: `${apiDomain}/raw`,
    API_TEST_ENDPOINT: `${apiDomain}/api/test/ping`,
    API_DOMAIN: apiDomain
  },
  // URLs para desarrollo local
  development: {
    API_URL: `${apiDomain}/api`,
    RAW_URL: `${apiDomain}/raw`,
    API_TEST_ENDPOINT: `${apiDomain}/api/test/ping`,
    API_DOMAIN: apiDomain
  }
};

// Exportar la configuracin segn el entorno
const currentConfig = isProd ? config.production : config.development;

export default currentConfig;