// src/services/syncService.js

// Servicio para gestionar la sincronizacin y el estado de la conexin
import { 
  getPendingPlatos, 
  updatePlatoSyncStatus, 
  getSyncQueue, 
  removeFromSyncQueue,
  getPlato
} from './indexedDBService';
import { adaptPlatoData } from '../utils/dataAdapters';
import { optimizeImageForSync } from './imageService';
import apiConfig from '../config/apiConfig';
import { testApiConnection, isOfflineMode } from '../utils/connectionHandler';

// Funcin para obtener las URLs actualizadas del API
const getApiUrls = () => {
  // Si el API URL fue actualizado por el connectionHandler, actualizar la configuracin
  const storedApiUrl = localStorage.getItem('apiUrl');
  if (storedApiUrl) {
    // Construir URLs con el dominio correcto
    return {
      API_URL: `${storedApiUrl}/api`,
      RAW_URL: `${storedApiUrl}/raw`
    };
  }
  
  // Usar configuracin por defecto
  return {
    API_URL: apiConfig.API_URL,
    RAW_URL: apiConfig.RAW_URL
  };
};

// URLs dinmicas que pueden actualizarse en tiempo de ejecucin
let { API_URL, RAW_URL } = getApiUrls();

// Funcin para actualizar las URLs de la API en tiempo de ejecucin
const refreshApiUrls = () => {
  const urls = getApiUrls();
  API_URL = urls.API_URL;
  RAW_URL = urls.RAW_URL;
  console.log(' URLs de API actualizadas:', { API_URL, RAW_URL });
};

// Escuchar los cambios de conexin para actualizar las URLs
window.addEventListener('api-connection-change', (event) => {
  console.log(' Estado de conexin API cambiado:', event.detail.connected);
  refreshApiUrls();
});

// Configuracin especfica para mejorar la sincronizacin
const SYNC_CONFIG = {
  skipServerAvailabilityCheck: true,
  maxPlatoSize: 30000, // 30KB mximo
  retryIntervals: [5000, 15000, 30000],
  maxRetries: 2,
  platoUrl: `${API_URL}/sync/platos`,
  emergencyPlatoUrl: `${API_URL}/platos/minimal`,
  useEmergencyEndpoint: true
};

// Estado de la sincronizacin
let isSyncing = false;
let _isOnline = navigator.onLine;

// Alternativa ms simple para verificar la conexin
const isOnline = async () => {
  // Primero comprobamos el estado de navigator.onLine
  if (!navigator.onLine) {
    return false;
  }
  
  // Intentamos hacer un fetch a un recurso pequeo (como un favicon)
  try {
    const response = await fetch(`${API_URL}/favicon.ico`, {
      method: 'HEAD',
      mode: 'no-cors',
      cache: 'no-store',
      credentials: 'omit'
    });
    
    return true; // Si no hay excepcin, asumimos que estamos en lnea
  } catch (error) {
    console.error('Error al verificar estado de conexin:', error);
    return false;
  }
};

// Configurar escuchas para eventos de conexin
const setupConnectionListeners = () => {
  window.addEventListener('online', async () => {
    console.log('Evento online detectado');
    _isOnline = await isOnline();
    if (_isOnline) {
      console.log('Conexin a Internet restablecida');
      // Sincronizar cuando se recupera la conexin
      syncData();
    }
  });
  
  window.addEventListener('offline', () => {
    console.log('Evento offline detectado');
    _isOnline = false;
    console.log('Conexin a Internet perdida');
  });
  
  // Verificar estado inicial
  isOnline().then(online => {
    console.log(`Estado inicial de conexin: ${online ? 'En lnea' : 'Fuera de lnea'}`);
  });
};

// Funcin para probar la conexin con el servidor (modo ultra-ligero)
const testServerConnection = async () => {
  console.log(' Probando conexin con el servidor (modo emergencia)...');
  
  try {
    // Usar XMLHttpRequest sin cookies ni headers personalizados
    const xhr = new XMLHttpRequest();
    
    const responsePromise = new Promise((resolve) => {
      const timeoutId = setTimeout(() => {
        console.warn(' Timeout en la prueba de conexin');
        xhr.abort();
        resolve(false);
      }, 5000);
      
      xhr.onreadystatechange = function() {
        if (xhr.readyState === 4) {
          clearTimeout(timeoutId);
          if (xhr.status >= 200 && xhr.status < 300) {
            console.log(' Conexin bsica establecida');
            resolve(true);
          } else if (xhr.status === 431) {
            console.error(' Error 431: Request Header Fields Too Large');
            console.log(' Necesitas reducir el tamao de las cookies o headers');
            resolve(false);
          } else {
            console.error(` Error en prueba de conexin: ${xhr.status}`);
            resolve(false);
          }
        }
      };
      
      xhr.onerror = function() {
        clearTimeout(timeoutId);
        console.error(' Error de red en prueba de conexin');
        // Intentar con endpoint de emergencia sin headers
        checkAlternativePort().then(resolve);
      };
      
      // Realizar una solicitud GET simple sin headers
      xhr.open('GET', `${API_URL}/test/ping`, true);
      // No aadir ningn header
      xhr.withCredentials = false; // Importante: evitar enviar cookies
      xhr.send();
    });
    
    return await responsePromise;
  } catch (error) {
    console.error(' Error en prueba de conexin:', error);
    return false;
  }
};

// Comprobar puerto alternativo
const checkAlternativePort = async () => {
  console.log(' Probando puertos alternativos...');
  
  // Lista de puertos comunes para probar
  const ports = [8080, 3000, 5000, 8000];
  
  for (const port of ports) {
    try {
      console.log(`Probando en puerto: ${port}`);
      const response = await fetch(`http://localhost:${port}/raw/ping`, {
        method: 'GET',
        mode: 'no-cors',
        cache: 'no-store',
        credentials: 'omit'
      });
      
      if (response.ok || response.status === 0) {
        console.log(` Servidor encontrado en puerto ${port}`);
        // Actualizar URL global para usar este puerto
        window.SERVER_PORT = port;
        return true;
      }
    } catch (error) {
      console.log(`Puerto ${port} no disponible`);
    }
  }
  
  console.error(' No se encontr el servidor en ningn puerto comn');
  return false;
};

// Funcin para probar la conexin a MySQL
const testMySQLConnection = async () => {
  console.log(' Probando conexin a MySQL...');
  
  try {
    const response = await fetch(`${API_URL}/test/db`, {
      method: 'GET',
      headers: {
        'Accept': 'application/json'
      }
    });
    
    if (response.ok) {
      const data = await response.json();
      console.log(' Conexin a MySQL establecida:', data);
      
      // Mostrar informacin clara sobre la base de datos
      if (data.data && data.data.database) {
        console.log(` Base de datos MySQL: ${data.data.database}`);
        console.log(` Tablas disponibles: ${data.data.tables.join(', ')}`);
      }
      
      return true;
    } else {
      console.error(' Error al conectar con MySQL:', await response.text());
      return false;
    }
  } catch (error) {
    console.error(' Error de red al probar la conexin MySQL:', error);
    return false;
  }
};

// Funcin para sincronizar datos entre IndexedDB y el servidor
const syncData = async () => {
  if (!await isOnline()) {
    console.log('Sin conexin a Internet, la sincronizacin no es posible');
    return false;
  }
  
  if (isSyncing) {
    console.log('Ya hay una sincronizacin en progreso');
    return false;
  }
  
  isSyncing = true;
  
  console.log(' Iniciando sincronizacin de datos...');
  
  try {
    // Procesar la cola de sincronizacin
    const syncQueue = await getSyncQueue();
    console.log(` Cola de sincronizacin: ${syncQueue.length} elementos`);
    
    for (const item of syncQueue) {
      if (item.entityType === 'plato') {
        // Obtener el plato completo de IndexedDB
        const plato = await getPlato(item.entityId);
        
        if (plato) {
          console.log(` Sincronizando plato: ${plato.name} (${plato.id})`);
          
          try {
            // Intentar sincronizacin minimalista
            const success = await syncPlato(plato);
            
            if (success.success) {
              console.log(` Plato ${plato.id} sincronizado correctamente`);
              await removeFromSyncQueue(item.id);
            } else {
              console.error(` Error al sincronizar plato ${plato.id}`);
            }
          } catch (syncError) {
            console.error(` Excepcin al sincronizar plato ${plato.id}:`, syncError);
          }
        } else {
          console.warn(` Plato ${item.entityId} no encontrado en IndexedDB`);
          await removeFromSyncQueue(item.id);
        }
      }
    }
    
    // Buscar platos pendientes que no estn en la cola
    console.log(' Buscando platos pendientes de sincronizacin...');
    const pendingPlatos = await getPendingPlatos();
    console.log(` Platos pendientes: ${pendingPlatos.length}`);
    
    for (const plato of pendingPlatos) {
      try {
        console.log(` Sincronizando plato pendiente: ${plato.name} (${plato.id})`);
        const success = await syncPlato(plato);
        
        if (success.success) {
          console.log(` Plato pendiente ${plato.id} sincronizado correctamente`);
        } else {
          console.error(` Error al sincronizar plato pendiente ${plato.id}: ${success.error}`);
        }
      } catch (syncError) {
        console.error(` Excepcin al sincronizar plato pendiente ${plato.id}: ${syncError}`);
      }
    }
    
    console.log(' Sincronizacin completada');
    return true;
  } catch (error) {
    console.error(' Error durante la sincronizacin:', error);
    return false;
  } finally {
    isSyncing = false;
  }
};

// Mtodo ultra-minimal usando XMLHttpRequest sin headers
const tryUltraMinimalSync = async (data, platoId) => {
  try {
    console.log(' Intentando sincronizacin ultra-minimal para plato:', platoId);
    
    // Crear un objeto XMLHttpRequest directamente
    const xhr = new XMLHttpRequest();
    
    // Configurar una promesa para manejar la respuesta
    const responsePromise = new Promise((resolve) => {
      // Configurar timeout
      const timeoutId = setTimeout(() => {
        console.warn(' Timeout en sincronizacin ultra-minimal');
        xhr.abort();
        resolve(false);
      }, 15000);
      
      xhr.onreadystatechange = async function() {
        if (xhr.readyState === 4) {
          clearTimeout(timeoutId);
          if (xhr.status >= 200 && xhr.status < 300) {
            console.log(' Sincronizacin ultra-minimal exitosa');
            try {
              await updatePlatoSyncStatus(platoId, 'synced');
            } catch (dbError) {
              console.error('Error al actualizar estado en BD:', dbError);
            }
            resolve(true);
          } else {
            console.error(` Error en sincronizacin ultra-minimal: ${xhr.status}`);
            resolve(false);
          }
        }
      };
      
      xhr.onerror = function() {
        clearTimeout(timeoutId);
        console.error(' Error de red en sincronizacin ultra-minimal');
        resolve(false);
      };
    });
    
    // Usar mtodo POST con datos minimalistas
    xhr.open('POST', `${API_URL}/platos/emergency-sync`, true);
    
    // Solo agregar Content-Type, nada ms
    xhr.setRequestHeader('Content-Type', 'application/json');
    
    // Preparar datos ultra-minimalistas
    const adaptedData = await adaptPlatoDataWithImageOptimization(data);
    const minimalData = {
      id: adaptedData.id,
      name: (adaptedData.name || "").substring(0, 30),
      price: Number(adaptedData.price) || 0
    };
    
    // Enviar los datos
    xhr.send(JSON.stringify(minimalData));
    
    return await responsePromise;
  } catch (error) {
    console.error(' Error en sincronizacin ultra-minimal:', error);
    return false;
  }
};

// Procesar la cola de sincronizacin
const processSyncQueue = async () => {
  if (!await isOnline() || isSyncing) {
    console.log(`No se puede procesar la cola. En lnea: ${_isOnline}, Sincronizando: ${isSyncing}`);
    return false;
  }
  
  isSyncing = true;
  console.log('Iniciando procesamiento de cola de sincronizacin...');
  
  try {
    const syncItems = await getSyncQueue();
    
    if (syncItems.length === 0) {
      console.log('No hay elementos en la cola de sincronizacin');
      isSyncing = false;
      return true;
    }
    
    console.log(`Procesando ${syncItems.length} elementos en la cola de sincronizacin`);
    
    // Procesar cada elemento de la cola
    for (const item of syncItems) {
      let syncSuccess = false;
      
      if (item.type === 'create' && item.entityType === 'plato') {
        syncSuccess = await syncPlatoToServer(item.data);
      }
      // Aqu pueden agregarse ms tipos de sincronizacin segn sea necesario
      
      if (syncSuccess) {
        // Eliminar de la cola si la sincronizacin fue exitosa
        await removeFromSyncQueue(item.id);
        console.log(`Elemento ${item.id} eliminado de la cola de sincronizacin`);
      } else {
        console.log(`No se pudo sincronizar el elemento ${item.id}, se reintentar ms tarde`);
      }
    }
    
    console.log('Procesamiento de cola de sincronizacin completado');
    return true;
  } catch (error) {
    console.error('Error al procesar cola de sincronizacin:', error);
    return false;
  } finally {
    isSyncing = false;
  }
};

// Mtodo de sincronizacin extremadamente simplificado para problemas persistentes
export const syncMinimalDataOnly = async (data, platoId, retryCount = 0) => {
  const maxRetries = SYNC_CONFIG.maxRetries;
  
  // Usar datos minimalistas pero incluir la imagen optimizada
  const adaptedData = await adaptPlatoDataWithImageOptimization(data);
  const minimalData = {
    id: adaptedData.id,
    name: adaptedData.name?.substring(0, 50) || 'Plato sin nombre',
    price: Number(adaptedData.price) || 0,
    description: adaptedData.description?.substring(0, 100) || '',
    category: adaptedData.category || 'principal',
    image: adaptedData.image, // Incluir la imagen optimizada
    is_available: Boolean(adaptedData.is_available),
    minimal_sync: true
  };
  
  return new Promise((resolve) => {
    // Establecer timeout para toda la operacin
    const operationTimeoutId = setTimeout(() => {
      console.warn(' Timeout global de la operacin de sincronizacin');
      resolve(false);
    }, 30000);
    
    const xhr = new XMLHttpRequest();
    
    // Configurar timeouts ms estrictos
    xhr.timeout = 15000;
    
    xhr.onreadystatechange = async function() {
      if (xhr.readyState === 4) {
        if (xhr.status >= 200 && xhr.status < 300) {
          clearTimeout(operationTimeoutId);
          try {
            await updatePlatoSyncStatus(platoId, 'synced');
            console.log(` Sincronizacin exitosa para plato ${platoId}`);
          } catch (error) {
            console.error('Error al actualizar estado:', error);
          }
          resolve(true);
        } else if (xhr.status === 0) {
          // Conexin abortada o no establecida
          console.warn(' Conexin interrumpida');
          handleRetry();
        } else if (xhr.status === 431) {
          console.error(` Error 431 (Headers demasiado grandes) - Intentando mtodo ultraminimal`);
          tryUltraMinimalSync(adaptedData, platoId).then(success => {
            clearTimeout(operationTimeoutId);
            resolve(success);
          });
        } else {
          console.error(` Error HTTP ${xhr.status} en sincronizacin`);
          handleRetry();
        }
      }
    };
    
    xhr.ontimeout = function() {
      console.warn(' Timeout de la peticin HTTP');
      handleRetry();
    };
    
    xhr.onerror = function() {
      console.error(' Error de red en sincronizacin');
      handleRetry();
    };
    
    // Funcin para manejar reintentos
    const handleRetry = () => {
      if (retryCount < maxRetries) {
        const delay = SYNC_CONFIG.retryIntervals[retryCount] || 5000;
        console.log(` Reintentando en ${delay}ms... (${retryCount + 1}/${maxRetries})`);
        
        setTimeout(() => {
          clearTimeout(operationTimeoutId);
          syncMinimalDataOnly(data, platoId, retryCount + 1)
            .then(success => resolve(success));
        }, delay);
      } else {
        clearTimeout(operationTimeoutId);
        console.warn(' Agotados todos los reintentos de sincronizacin');
        resolve(false);
      }
    };
    
    try {
      // Usar mtodo POST con headers mnimos
      xhr.open('POST', `${API_URL}/platos`, true);
      xhr.setRequestHeader('Content-Type', 'application/json');
      
      // Enviar los datos minimalistas
      xhr.send(JSON.stringify(minimalData));
    } catch (error) {
      console.error(' Error al iniciar la peticin:', error);
      handleRetry();
    }
  });
};

// Sincronizar un plato con el servidor
const syncPlatoToServer = async (plato, retryCount = 0) => {
  const maxRetries = SYNC_CONFIG.maxRetries;
  
  // Si el plato est marcado como problemtico, omitirlo
  if (plato.sync_problematic) {
    console.warn(` Omitiendo plato ${plato.id} marcado como problemtico para sincronizacin`);
    return false;
  }
  
  console.log(`Sincronizando plato ${plato.id} con el servidor (intento ${retryCount + 1}/${maxRetries + 1})...`);
  
  try {
    // Omitir verificacin si est desactivada
    if (!SYNC_CONFIG.skipServerAvailabilityCheck) {
      // Verificar si el servidor est disponible
      const serverAvailable = await checkServerAvailability();
      if (!serverAvailable) {
        console.error(' El servidor no est disponible. Posponiendo sincronizacin.');
        return false;
      }
    }
    
    // Preparar datos para sincronizacin incluyendo la imagen
    let adaptedData = await adaptPlatoDataWithImageOptimization(plato);
    let syncData = {
      id: adaptedData.id,
      name: adaptedData.name?.substring(0, 50) || 'Plato sin nombre',
      price: Number(adaptedData.price) || 0,
      description: adaptedData.description?.substring(0, 100) || '',
      availableQuantity: Number(adaptedData.availableQuantity) || 0,
      is_available: Boolean(adaptedData.is_available),
      image: adaptedData.image || null, // Incluir la imagen
      minimal_sync: true
    };
    
    // Usar el mtodo ms confiable para sincronizacin
    return await syncPlato(adaptedData);
  } catch (error) {
    console.error(' Error en sincronizacin:', error);
    
    if (retryCount >= maxRetries) {
      try {
        await markPlatoAsSyncProblematic(plato.id);
      } catch (markError) {
        console.error('Error al marcar plato como problemtico:', markError);
      }
    }
    
    return false;
  }
};

// Verificar estado de sincronizacin con el backend
const checkSyncStatus = async () => {
  try {
    console.log(' Verificando estado de sincronizacin...');
    
    const response = await fetch(`${API_URL}/sync/status`, {
      method: 'GET',
      headers: { 'Accept': 'application/json' },
      credentials: 'omit' // Importante: no enviar cookies
    });
    
    if (response.ok) {
      const data = await response.json();
      console.log(' Estado de sincronizacin:', data);
      
      // Si no existe la tabla, intentar crearla
      if (!data.data.mysql.tablaPlatos) {
        console.log(' La tabla platos no existe, intentando configurar...');
        await setupSync();
      }
      
      return data.data;
    } else {
      console.error(' Error al verificar estado de sincronizacin:', await response.text());
      return null;
    }
  } catch (error) {
    console.error(' Error al verificar estado de sincronizacin:', error);
    return null;
  }
};

// Configurar la sincronizacin - crear tablas necesarias
const setupSync = async () => {
  try {
    console.log(' Configurando sincronizacin...');
    
    const response = await fetch(`${API_URL}/sync/setup`, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      credentials: 'omit',
      body: JSON.stringify({ force: false })
    });
    
    if (response.ok) {
      const data = await response.json();
      console.log(' Configuracin de sincronizacin completada:', data);
      return true;
    } else {
      console.error(' Error al configurar sincronizacin:', await response.text());
      return false;
    }
  } catch (error) {
    console.error(' Error al configurar sincronizacin:', error);
    return false;
  }
};

// Inicializar el servicio de sincronizacin
const initSyncService = () => {
  console.log(' Inicializando servicio de sincronizacin...');
  
  // Probar conexiones y configurar sincronizacin
  Promise.all([
    testServerConnection(),
    checkSyncStatus()
  ]).then(([isApiConnected, syncStatus]) => {
    console.log(` Estado de conexin directa - API: ${isApiConnected}`);
    
    if (isApiConnected) {
      // Configurar escuchas para eventos de conexin
      setupConnectionListeners();
      
      // Si hay estado de sincronizacin, proceder con la sincronizacin
      if (syncStatus && syncStatus.mysql && syncStatus.mysql.connected) {
        console.log(' MySQL conectado, procediendo con sincronizacin');
        syncData();
        
        // Reactivar la sincronizacin peridica (cada 1 minuto)
        const syncInterval = setInterval(syncData, 60000);
      } else {
        console.log(' MySQL no est configurado correctamente, intentando configurar...');
        setupSync().then(success => {
          if (success) {
            console.log(' Configuracin exitosa, iniciando sincronizacin');
            syncData();
            
            // Reactivar la sincronizacin peridica (cada 1 minuto)
            const syncInterval = setInterval(syncData, 60000);
          }
        });
      }
    } else {
      console.error(' No se pudo establecer conexin con el servidor.');
    }
  });
  
  console.log(' Servicio de sincronizacin inicializado');
};

// Exportar las funciones necesarias
export {
  initSyncService,
  syncData,
  isOnline,
  _isOnline,
  testServerConnection,
  testMySQLConnection,
  checkSyncStatus,
  setupSync,
  syncPlato,
  adaptPlatoDataWithImageOptimization
};

// Asegrate de que la funcin que maneja la sincronizacin tenga un mejor manejo de errores

/**
 * Funcin para sincronizar un plato con el servidor, asegurando que la imagen se incluya
 * @param {Object} plato - El plato a sincronizar
 * @returns {Promise<Object>} - Resultado de la sincronizacin
 */
const syncPlato = async (plato) => {
  console.log(` Sincronizando plato: ${plato.name} (ID: ${plato.id})`);
  
  try {
    // Preparar los datos para enviar (incluir la imagen optimizada)
    const platoToSync = await adaptPlatoDataWithImageOptimization(plato);
    
    // Omitir la imagen en los logs para evitar mensajes demasiado largos
    console.log(' Datos a enviar:', { 
      ...platoToSync, 
      image: platoToSync.image ? 'Imagen en base64 (omitida en log)' : null 
    });
    
    // Imprimir la URL exacta para depuracin
    const url = SYNC_CONFIG.platoUrl; // Usar la configuracin existente
    console.log(` URL de sincronizacin: ${url}`);
    
    const response = await fetch(url, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(platoToSync),
      // Evitar que se incluyan cookies u otros datos de autenticacin si no son necesarios
      credentials: 'omit'
    });
    
    // Verificar si la respuesta es exitosa
    if (!response.ok) {
      const errorText = await response.text();
      console.error(` Error de sincronizacin (${response.status}): ${errorText}`);
      
      // Intentar con el mtodo de sincronizacin minimalista si hay un error
      if (response.status >= 400) {
        console.log(' Intentando sincronizacin minimalista como fallback...');
        return await syncMinimalDataOnly(plato);
      }
      
      return {
        success: false,
        message: `Error de servidor: ${response.status} ${response.statusText}`,
        error: errorText
      };
    }
    
    // Procesar la respuesta exitosa
    const responseData = await response.json();
    console.log(' Sincronizacin exitosa:', responseData);
    
    // Actualizar el estado de sincronizacin en IndexedDB
    try {
      await updatePlatoSyncStatus(plato.id, true);
      console.log(` Estado de sincronizacin actualizado para plato ID: ${plato.id}`);
    } catch (syncStatusError) {
      console.warn(` No se pudo actualizar el estado de sincronizacin: ${syncStatusError.message}`);
    }
    
    return {
      success: true,
      message: 'Plato sincronizado correctamente',
      data: responseData
    };
  } catch (error) {
    console.error(' Error en sincronizacin:', error);
    
    // Intentar con el mtodo de sincronizacin minimalista si hay un error
    console.log(' Intentando sincronizacin minimalista como fallback...');
    return await syncMinimalDataOnly(plato);
  }
};

// Funcin para adaptar los datos del plato antes de sincronizar, con optimizacin de imgenes
const adaptPlatoDataWithImageOptimization = async (plato) => {
  // Crear una copia para no modificar el original
  const adaptedData = { ...plato };
  
  // Asegurarse de que todos los campos necesarios estn presentes
  adaptedData.id = plato.id;
  adaptedData.name = plato.name || 'Plato sin nombre';
  adaptedData.price = parseFloat(plato.price) || 0;
  adaptedData.description = plato.description || '';
  adaptedData.category = plato.category || 'principal';
  
  // Optimizar la imagen si existe
  if (plato.image && typeof plato.image === 'string' && plato.image.length > 0) {
    try {
      adaptedData.image = await optimizeImageForSync(plato.image);
      console.log(`Imagen optimizada para ${plato.name}`);
    } catch (error) {
      console.warn(`Error al optimizar imagen para ${plato.name}:`, error);
      adaptedData.image = plato.image; // Usar la original en caso de error
    }
  } else {
    adaptedData.image = plato.image || null;
  }
  
  adaptedData.image_url = plato.image_url || '';
  adaptedData.is_available = plato.is_available !== false;
  adaptedData.includesDrink = plato.includesDrink || false;
  adaptedData.availableQuantity = plato.availableQuantity || 0;
  
  return adaptedData;
};

/**
 * Actualiza el estado de sincronizacin de un elemento
 * @param {string} id - ID del elemento
 * @param {string|boolean} status - Estado de sincronizacin ('pending', 'synced', 'pending_deletion' o un booleano)
 * @returns {Promise<boolean>} - true si se actualiz correctamente
 */
export async function updateSyncStatus(id, status) {
  try {
    // Validar que el status sea uno de los valores permitidos o convertir booleano a string
    let validStatus = status;
    
    // Si es booleano, convertirlo al estado correspondiente
    if (typeof status === 'boolean') {
      validStatus = status === true ? 'synced' : 'pending';
    }
    
    // Ahora validar que sea uno de los estados permitidos
    if (!['pending', 'synced', 'pending_deletion'].includes(validStatus)) {
      console.warn(` No se pudo actualizar el estado de sincronizacin: Estado invlido: ${status}. Debe ser uno de: pending, synced, pending_deletion`);
      return false;
    }
    
    // Continuar con la lgica existente para actualizar el estado
    // ...existing code...
    
    return true;
  } catch (error) {
    console.error('Error al actualizar estado de sincronizacin:', error);
    return false;
  }
}