import axios from 'axios';
import apiConfig from '../config/apiConfig';

const API_URL = apiConfig.API_URL;

// Funcin para guardar token en localStorage
const saveToken = (token) => {
  localStorage.setItem('token', token);
};

// Funcin para obtener token de localStorage
const getToken = () => {
  return localStorage.getItem('token');
};

// Funcin para eliminar token de localStorage
const removeToken = () => {
  localStorage.removeItem('token');
};

// Configurar Axios
const setupAxiosInterceptors = () => {
  // Interceptor de solicitudes
  axios.interceptors.request.use(
    (config) => {
      const token = getToken();
      if (token) {
        config.headers.Authorization = `Bearer ${token}`;
      }
      return config;
    },
    (error) => {
      return Promise.reject(error);
    }
  );
  
  // Interceptor de respuestas
  axios.interceptors.response.use(
    (response) => {
      return response;
    },
    (error) => {
      // Si el error es 401, puede ser que el token expir
      if (error.response && error.response.status === 401) {
        removeToken();
        // Redirigir a la pgina de login (esto debe ser manejado por el router)
        if (window.location.pathname !== '/login') {
          window.location.href = '/login';
        }
      }
      return Promise.reject(error);
    }
  );
};

// Inicializar interceptores
setupAxiosInterceptors();

// Funcin para iniciar sesin
async function login(email, password) {
  console.log('Intentando iniciar sesin con:', email);
  
  try {
    // Limpiar cualquier informacin de usuario anterior
    removeToken();
    localStorage.removeItem('user');
    localStorage.removeItem('isLoggedIn');
    
    // Intentar iniciar sesin con el backend
    console.log('Enviando solicitud al backend:', `${API_URL}/auth/login`);
    const response = await axios.post(`${API_URL}/auth/login`, {
      email,
      password
    }, {
      headers: {
        'Content-Type': 'application/json'
      },
      // No enviar cookies
      withCredentials: false,
      // Aumentar el timeout para evitar errores de conexin
      timeout: 10000
    });
    
    console.log('Respuesta del backend:', response.status, response.data);
    
    if (response.status === 200 && response.data.success) {
      console.log('Login exitoso con el backend');
      // Guardar token
      const token = response.data.data.token;
      saveToken(token);
      
      // Actualizar el token en el servicio API
      import('./apiService').then(apiService => {
        console.log('Actualizando token en apiService');
        apiService.default.setToken(token);
      });
      
      // Guardar informacin del usuario en localStorage
      localStorage.setItem('user', JSON.stringify(response.data.data.user));
      
      // Establecer explcitamente el estado de autenticacin
      localStorage.setItem('isLoggedIn', 'true');
      
      return {
        success: true,
        user: response.data.data.user
      };
    } else {
      console.error('Respuesta inesperada del servidor:', response.data);
      throw new Error('Respuesta inesperada del servidor');
    }
  } catch (error) {
    // Verificar si el error es de red o del servidor
    if (error.response) {
      // El servidor respondi con un cdigo de estado fuera del rango 2xx
      console.error('Error de respuesta del servidor:', error.response.status, error.response.data);
      
      // Verificar si es un error especfico de usuario desactivado
      if (error.response.data && error.response.data.message) {
        // Si el mensaje contiene la palabra "desactivado", mostrar ese mensaje especfico
        if (error.response.data.message.includes('desactivado')) {
          throw new Error(error.response.data.message);
        }
      }
    } else if (error.request) {
      // La solicitud se realiz pero no se recibi respuesta
      console.error('No se recibi respuesta del servidor:', error.request);
    } else {
      // Algo sucedi al configurar la solicitud
      console.error('Error al configurar la solicitud:', error.message);
    }
    
    console.log('Fallback a autenticacin local...');
    
    // Datos de usuarios locales para desarrollo/pruebas
    const localUsers = [
      {
        id: 1,
        email: 'superadmin@example.com',
        password: '123456',
        nombre: 'Super Administrador',
        roles: ['Superadministrador']
      },
      {
        id: 2,
        email: 'admin@example.com',
        password: '123456',
        nombre: 'Administrador',
        roles: ['Administrador']
      },
      {
        id: 3,
        email: 'empleado@example.com',
        password: '123456',
        nombre: 'Empleado',
        roles: ['Empleado']
      },
      // Aadir el usuario alex para pruebas locales
      {
        id: 5,
        email: 'alex@gmail.com',
        password: 'password',
        nombre: 'alex',
        roles: ['Empleado']
      }
    ];
    
    // Buscar usuario local
    const user = localUsers.find(u => u.email === email && u.password === password);
    
    if (user) {
      console.log('Login exitoso con usuario local:', user.email);
      // Si el usuario local es vlido, establecer el estado de autenticacin
      localStorage.setItem('isLoggedIn', 'true');
      
      // Crear un token local
      const localToken = `local_${btoa(JSON.stringify(user))}`;
      saveToken(localToken);
      
      // Actualizar el token en el servicio API
      import('./apiService').then(apiService => {
        console.log('Actualizando token en apiService (local)');
        apiService.default.setToken(localToken);
      });
      
      // Guardar usuario en localStorage
      localStorage.setItem('user', JSON.stringify({
        id: user.id,
        email: user.email,
        nombre: user.nombre,
        roles: user.roles
      }));
      
      return {
        success: true,
        user: {
          id: user.id,
          email: user.email,
          nombre: user.nombre,
          roles: user.roles
        }
      };
    }
    
    // Si no se encontr usuario local
    throw new Error('Credenciales incorrectas');
  }
}

// Funcin para cerrar sesin
const logout = () => {
  removeToken();
  localStorage.removeItem('user');
  localStorage.removeItem('isLoggedIn');
};

// Funcin para verificar si el usuario est autenticado
const isAuthenticated = () => {
  const token = getToken();
  const isLoggedInFlag = localStorage.getItem('isLoggedIn') === 'true';
  return !!token && isLoggedInFlag;
};

// Funcin para obtener el usuario actual
const getCurrentUser = () => {
  try {
    const userStr = localStorage.getItem('user');
    if (!userStr) {
      console.log('No hay usuario en localStorage');
      return null;
    }
    
    const user = JSON.parse(userStr);
    console.log('Usuario actual obtenido de localStorage:', user.email);
    return user;
  } catch (error) {
    console.error('Error al obtener el usuario actual:', error);
    return null;
  }
};

// Funcin para verificar si el usuario tiene un rol especfico
const hasRole = (role) => {
  const user = getCurrentUser();
  return user && user.roles && user.roles.includes(role);
};

// Funcin para verificar si el usuario es administrador
const isAdmin = () => {
  return hasRole('Administrador') || hasRole('Superadministrador');
};

// Funcin para verificar si el usuario es superadministrador
const isSuperAdmin = () => {
  return hasRole('Superadministrador');
};

export {
  login,
  logout,
  isAuthenticated,
  getCurrentUser,
  hasRole,
  isAdmin,
  isSuperAdmin,
  getToken,
  removeToken
};
